import './token-specimen';
import type { TokenObject, TokensStructure } from '../types';

type TokenCategory = 'background' | 'content' | 'border';

class Tier2Colors extends HTMLElement {
	private tokens: TokensStructure | null = null;

	constructor() {
		super();
	}

	connectedCallback() {
		this.loadTokens();
		this.render();

		document.addEventListener('DOMContentLoaded', () => {
			setTimeout(() => {
				this.loadTokens();
				this.render();
			}, 10);
		});
	}

	private loadTokens(): void {
		if (window.ouiTokens) {
			this.tokens = window.ouiTokens;
		}
	}

	private filterTokens(category: TokenCategory): TokenObject[] {
		if (!this.tokens || !this.tokens.tier2 || !this.tokens.tier2.colors) {
			return [];
		}
		return this.tokens.tier2.colors[category] || [];
	}

	private renderTokenGroup(tokens: TokenObject[]): string {
		if (!tokens || tokens.length === 0) {
			return '<p>No tokens available</p>';
		}

		return tokens
			.map((token) => {
				const cssVariable = token.value.match(/var\(([^)]+)\)/)?.[1] || token.cssVar;
				const computedValue =
					getComputedStyle(document.documentElement).getPropertyValue(cssVariable).trim() || token.value;

				return `
        <token-specimen
          semantic-name="${token.name}"
          css-variable="${cssVariable}"
          value="${computedValue}"
          variant="color"
        ></token-specimen>
      `;
			})
			.join('');
	}

	private findKnockoutBackground(contentTokenName: string): string | null {
		const backgroundTokenName = contentTokenName.replace('content-', 'background-');
		const backgroundTokens = this.filterTokens('background');
		const matchingBgToken = backgroundTokens.find((token) => token.name === backgroundTokenName);

		if (matchingBgToken) {
			const bgCssVariable = matchingBgToken.value.match(/var\(([^)]+)\)/)?.[1] || matchingBgToken.cssVar;
			return getComputedStyle(document.documentElement).getPropertyValue(bgCssVariable).trim() || matchingBgToken.value;
		}

		return null;
	}

	private renderContentTokenGroup(tokens: TokenObject[]): string {
		if (!tokens || tokens.length === 0) {
			return '<p>No tokens available</p>';
		}

		return tokens
			.map((token) => {
				const cssVariable = token.value.match(/var\(([^)]+)\)/)?.[1] || token.cssVar;
				const computedValue =
					getComputedStyle(document.documentElement).getPropertyValue(cssVariable).trim() || token.value;

				const isKnockout = token.name.includes('knockout');
				const knockoutBg = isKnockout ? this.findKnockoutBackground(token.name) : null;

				return `
        <token-specimen
          semantic-name="${token.name}"
          css-variable="${cssVariable}"
          value="${computedValue}"
          variant="content"
          ${knockoutBg ? `knockout-background="${knockoutBg}"` : ''}
        ></token-specimen>
      `;
			})
			.join('');
	}

	private renderBorderTokenGroup(tokens: TokenObject[]): string {
		if (!tokens || tokens.length === 0) {
			return '<p>No tokens available</p>';
		}

		return tokens
			.map((token) => {
				const cssVariable = token.value.match(/var\(([^)]+)\)/)?.[1] || token.cssVar;
				const computedValue =
					getComputedStyle(document.documentElement).getPropertyValue(cssVariable).trim() || token.value;

				return `
        <token-specimen
          semantic-name="${token.name}"
          css-variable="${cssVariable}"
          value="${computedValue}"
          variant="color"
        ></token-specimen>
      `;
			})
			.join('');
	}

	private render(): void {
		const backgroundTokens = this.filterTokens('background');
		const contentTokens = this.filterTokens('content');
		const borderTokens = this.filterTokens('border');

		this.innerHTML = `
      <section class="section">
        <header class="section__header">
          <h2 class="section__heading">Background Colors</h2>
        </header>
        <div class="token-list">
          ${this.renderTokenGroup(backgroundTokens)}
        </div>
      </section>

      <section class="section">
        <header class="section__header">
          <h2 class="section__heading">Content Colors</h2>
        </header>
        <div class="token-list">
          ${this.renderContentTokenGroup(contentTokens)}
        </div>
      </section>

      <section class="section">
        <header class="section__header">
          <h2 class="section__heading">Border Colors</h2>
        </header>
        <div class="token-list">
          ${this.renderBorderTokenGroup(borderTokens)}
        </div>
      </section>
    `;
	}
}

if (!customElements.get('tier-2-colors')) {
	customElements.define('tier-2-colors', Tier2Colors);
}

export default Tier2Colors;
